* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.content {
    height: 80vh;
}

.register-page, .login-page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
}

/* Homepage */
.nav {
    background-color: rgb(18, 155, 18);
}

.logo1 {
    height: 60px;
    margin: 18px auto 10px 20px;
}

.logout {
    margin: 10px 20px 0 auto;
}

@media screen and (max-width: 768px) {
    .logo1 {
        height: 40px;
        margin: 18px 0 10px 0;
    }
    
    .logout {
        margin: 0;
    }
}
/* Homepage */

/* Register */
.navbar1 {
    height: 100px;
    background-color: rgb(18, 155, 18)
}

.navbar1 img {
    height: 70px;
    margin: 15px 0px 10px 20px;
}
/* Register */

/* Transection Page */
.filters {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    box-shadow: 0 0 3px gray;
}
/* Transection Page */

/* antd icons */
.switch-icon {
    border: 1px solid rgba(0, 0, 0, 0.677);
    border-radius: 15px;
    padding: 10px 15px;
}

.anticon svg {
    font-size: 20px;
    cursor: pointer;
}

.active-icon {
    color: black;
}

.inactive-icon {
    color: grey;
}

@media screen and (max-width: 425px) {
    .switch-icon {
        border: 1px solid rgba(0, 0, 0, 0.677);
        border-radius: 15px;
        padding: 3px 3px;
    }
}
/* antd icons */

/* Home */
.head {
    height: 100px;
    background-color: rgb(18, 155, 18);
    border-bottom: 3px solid black;
}

.logo {
    height: 60px;
    margin: 18px auto 10px 20px;
}

.login, .register {
    padding: 30px 50px 0 0;
}

.page {
    height: 90vh;
}

.body {
    background-color: rgba(18, 155, 18);
    width: 1600px;
    height: 735px;
    margin-top: 100px;
    border-radius: 25px;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(2, 2fr) 1fr repeat(2, 2fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.overview {
    grid-area: 1 / 1 / 3 / 6;
    padding: 20px 30px;
    background-color: rgb(160, 152, 152);
    margin: 35px;
    border-radius: 25px;
}

.overview h1 {
    font-size: 50px;
}

.overview p {
    font-size: 25px;
}

.display {
    grid-area: 1 / 6 / 3 / 9;
}

.display img {
    width: 500px;
    margin: 20px auto;
}

.features {
    grid-area: 3 / 1 / 4 / 9;
    text-align: center;
}

.features h1 {
    font-size: 60px;
}

.feature1 h2, .feature2 h2, .feature3 h2, .feature4 h2 {
    font-size: 20px;
}

.feature1 p, .feature2 p, .feature3 p, .feature4 p {
    font-size: 18px;
}

.feature1 {
    grid-area: 4 / 1 / 6 / 3;
    padding: 20px 25px;
    background-color: rgb(160, 152, 152);
    margin: 35px;
    border-radius: 25px;
}

.feature2 {
    grid-area: 4 / 3 / 6 / 5;
    padding: 20px 25px;
    background-color: rgb(160, 152, 152);
    margin: 35px;
    border-radius: 25px;
}

.feature3 {
    grid-area: 4 / 5 / 6 / 7;
    padding: 20px 25px;
    background-color: rgb(160, 152, 152);
    margin: 35px;
    border-radius: 25px;
}

.feature4 {
    grid-area: 4 / 7 / 6 / 9;
    padding: 20px 25px;
    background-color: rgb(160, 152, 152);
    margin: 35px;
    border-radius: 25px;
}

